* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600&family=Quicksand:wght@300&family=Rowdies:wght@300;400&display=swap");

body {
  overflow: none;
  background-color: white;
  max-height: 100vh;
  align-items: center;
  font-family: Poppins;
}
.container {
  overflow: hidden;
  width: 550px;
  margin: 0 auto;

  background-color: white;
}
.navbar {
  display: flex;
  align-items: center;
  width: 1000px;
  height: 70px;
  background: #ffffff;
  box-shadow: 0px 2.98256px 15.4564px rgba(48, 48, 48, 0.068);
}

.navbar img {
  padding: 30px;
}
.contents img {
  object-fit: cover;
  display: flex;
  margin: 26px auto;
}
.contents h1 {
  padding: 0 30px;
  font-family: Poppins;
  font-style: normal;
  font-weight: 600;
  font-size: 36px;
}
.contents p {
  width: 400px;

  padding: 10px 30px;
  font-family: Poppins;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
}
.card-container {
  display: flex;
}
.card {
  pointer-events: none;
  cursor: pointer;
}

.card-content {
  cursor: pointer;
  margin: 10px 28px;
  margin-right: -10px;
}

.card img {
  border-radius: 10px;
  object-fit: cover;
}
.card p {
  cursor: pointer;
  margin: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: Poppins;
  font-style: normal;
  font-weight: normal;
  font-size: 10.24px;
  z-index: 100;
  width: 61px;
  height: 20px;

  background: #ffffff;
  border-radius: 2px;
  position: absolute;
}
.card-container {
  font-size: 12px;
}
.info-container {
  display: flex;
  align-items: center;
}
.rate {
  font-family: Poppins;
  font-style: normal;
  font-weight: 300;
  font-size: 12px;
  padding: 0 3px;
}
.times,
.dot,
.country {
  display: flex;
  align-items: center;
  margin-right: 1px;
  font-family: Poppins;
  font-style: normal;
  font-weight: 300;
  font-size: 12px;
  color: #918e9b;
}
.des-container .title {
  cursor: pointer;
  font-family: Poppins;
  padding: 8px 0;
  font-style: normal;
  font-weight: 300;
  font-size: 12px;
  line-height: 110%;

  color: #222222;
}
.des-container .price {
  font-family: Poppins;
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 110%;

  color: #222222;
}
.des-container .price span {
  font-family: Poppins;
  font-style: normal;
  font-weight: 400;
  font-size: 12px;

  color: #222222;
}
